import React, { useState } from 'react';
import { LanguageContext } from '../contexts/LanguageContext';
import './Slideshow.css';
import { FaArrowLeft, FaArrowRight } from "react-icons/fa";


const Slideshow = (props) => {
    const [currentDisplayedImage, setCurrentDisplayedImage] = useState(0);

    const goBack = () => {
        let lastImage = props.images.length - 1;
        //se a imagem atual tem indeice > do q a primeira, retrocede
        if (currentDisplayedImage > 0) {
            setCurrentDisplayedImage(currentDisplayedImage - 1);
        }//se a imagem atual ja é a primeira, dáa volta e vai para o fim 
        else {
            setCurrentDisplayedImage(lastImage);
        }
    }

    const goForward = () => {
        let lastImage = props.images.length - 1;
        // se a imagem atual é a ultima da lista, volta ao inicio
        if (currentDisplayedImage === lastImage) {
            setCurrentDisplayedImage(0);
        }//se a imagem atual n é a ultima da lista, continua a avançar
        else {
            setCurrentDisplayedImage(currentDisplayedImage + 1);
        }
    }
    //console.log(currentDisplayedImage, " currentDisplayedImage");

    return (
        <LanguageContext.Consumer>
            {({ texts, currentLanguage, toggleLanguage }) => {
                return (
                    <div style={styles.slideshowBox}>
                        <div style={styles.holder}>

                            <img src={props.images[currentDisplayedImage]} alt="Project" className="image img-thumbnail img-fluid" />

                            <div className="btn-group controls" role="group" aria-label="controls">

                                <button className="btn btn-default shadow-none"
                                    style={styles.arrow}
                                    onClick={goBack}><FaArrowLeft /></button>
                                <button className="btn btn-default shadow-none"
                                    style={styles.arrow}
                                    onClick={goForward}><FaArrowRight /></button>

                            </div>


                        </div>
                    </div>
                )
            }
            }
        </LanguageContext.Consumer>
    );
}
const styles = {
    slideshowBox: {
        width: '90%', //era %80     
        backgroundColor: 'transparent',
        margin: 'auto',
    },
    holder: {
        //display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'row',
        backgroundColor: 'transparent',
        margin: 'auto',
    },

}

export default Slideshow;

