import React, { useState,/* Fragment,*/ useRef, useEffect } from 'react';
import Modal from './Modal';
import Slideshow from './Slideshow';

const Work = (props) => {

    const portfolioEntry = props.portfolioEntry;

    const out = useRef();

    const [activate, setActivate] = useState(false);

    const toggle = () => {
        activate ? setActivate(false) : setActivate(true);
    }

    useEffect(() => {
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);


    const handleClickOutside = event => {
        if (out.current && !out.current.contains(event.target)) {
            setActivate(false);
        }
    };

    return (
        <div ref={out}>
            <img src={portfolioEntry['imgSrc']} alt={portfolioEntry['imgAlt']}
                className="image img-thumbnail" />
            <button className="btn btn-default"
                onClick={toggle}
            >{props.openText}
            </button>
            <Modal show={activate} onClose={toggle}>
                <Slideshow images={portfolioEntry.modalImages}
                    className="image img-thumbnail" />
            </Modal>
        </div>

    )
}
export default Work;

