import React, { useState, useRef, useEffect } from 'react';
import Modal from './Modal';
import './Details.css';

const Details = (props) => {

    const elemt = props.elm;

    const out = useRef();
    const [activate, setActivate] = useState(false);

    const toggle = () => {
        activate ? setActivate(false) : setActivate(true);
    }

    useEffect(() => {
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);


    const handleClickOutside = event => {
        if (out.current && !out.current.contains(event.target)) {
            setActivate(false);
        }
    };

    return (
        <div className="certs" ref={out} onClick={toggle}>
            {elemt.title}
            {/* <button className="courses"
                onClick={toggle} >{elemt.title}</button> */}
            <Modal show={activate} onClose={toggle}>
                <div className="project-description" style={styles.descrip}>
                    {/*elemt.description*/}

                    {
                        elemt.description.map(function (elemt, index) {

                            return <div className="list"><li>{elemt}</li></div>
                        })}


                </div>
            </Modal>
        </div>
    )
}

const styles = {
    descrip: {
        backgroundColor: 'hotpink',
        height: '20vw',
        borderRadius: 20,
        color: 'white',
    },
    txt: {

    }
}
export default Details;

