import React, { useState, useRef, useEffect } from 'react';
import './CertificationsAndCourses.css';
import { descriptions } from '../constants/constants';

import Details from './Details';

const CertificationsAndCourses = () => {

    const outer = useRef();
    const [activate, setActivate] = useState(false);

    const toggle = () => {
        activate ? setActivate(false) : setActivate(true);
    }

    useEffect(() => {
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);


    const handleClickOutside = event => {
        if (outer.current && !outer.current.contains(event.target)) {
            setActivate(false);
        }
    };
    return (
        <div className="container my-5" ref={outer}>
            <div className="container my-5">
                <div className="row">

                    {descriptions.map(function (elem) {
                        return (

                            <Details elm={elem} />

                        )
                    })
                    }




                </div>
            </div>

        </div>
    )
}
export default CertificationsAndCourses; 