import React from 'react';
import './Work.css';
import { portfolioEntries } from '../constants/constants';
import { LanguageContext } from '../contexts/LanguageContext';
import Work from './Work';

const WorkList = () => {


    return (
        <LanguageContext.Consumer>
            {({ texts }) => {
                return (
                    <div className="container" >
                        <div className="container my-5">
                            <div className="row">
                                {
                                    portfolioEntries.map((portfolioEntry, index) => {
                                        return (
                                            <div className="col-12 col-sm-12 col-md-6 col-lg-4  mb-4"
                                                key={index}>
                                                <Work portfolioEntry={portfolioEntry} openText={texts.alertButton.open} />
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    </div>
                )
            }}
        </LanguageContext.Consumer>
    )

}
export default WorkList;

