import React from 'react';
import { HashRouter, Route } from 'react-router-dom';
import Profile from '../components/Profile';
import Home from '../components/Home';
import WorkList from '../components/WorkList';
import Contacts from '../components/Contacts';
import CertificationsAndCourses from '../components/CertificationsAndCourses';



const Routes = () => {
    return (
        <HashRouter basename='/' >
            <Route exact path='/' component={Home} />
            <Route path='/profile' component={Profile} />
            <Route path='/work' component={WorkList} />
            <Route path='/contacts' component={Contacts} />
            <Route path='/about' component={Profile} />
            <Route path='/certifications-and-courses' component={CertificationsAndCourses} />
        </HashRouter>

    )
}

export default Routes;