import React, { Component } from 'react';
import './Profile.css';
import { LanguageContext } from '../contexts/LanguageContext';


class Profile extends Component {
    render() {
        return (

            <LanguageContext.Consumer>
                {({ texts, currentLanguage, toggleLanguage }) => {
                    console.log(texts, currentLanguage, toggleLanguage);

                    return (
                        <div>
                            <div className="container">

                                <div className="row">
                                    <div className="col col-md-12" id="myprofile">
                                        {texts.about.about1}<br />
                                        {texts.about.about2}
                                    </div>
                                </div>
                            </div>



                        </div>
                    )

                }}
            </LanguageContext.Consumer>
        )
    }
}
export default Profile;