import React from 'react';
import './Contactme.css';
import Contactme2 from './Contactme2';
import { LanguageContext } from '../contexts/LanguageContext';

const Contacts = () => {

    return (
        <LanguageContext.Consumer>
            {({ texts }) => {
                return (

                    <div className="container">
                        <div className="row">
                            <div className="col-md-6" id="divcontacts1">

                                <div className="contacts-text">

                                    <div className="headertxt"> {texts.contactme.title1}</div>
                                    <div className="lead">{texts.contactme.title2}</div>
                                    <div className="techs">
                                        {texts.contactme.title3}
                                        <br />
                                        {texts.contactme.title4}
                                        <a href="https://github.com/joanafpt"
                                            target="_blank" rel="noopener noreferrer"> Github.</a>
                                    </div>
                                </div>
                            </div>

                            <div className="col-md-6" id="divcontacts2">
                                <Contactme2 />
                            </div>


                        </div>

                    </div>


                )

            }}
        </LanguageContext.Consumer>

    )
}
export default Contacts;