import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import Button from './Button';
import './Experimental.css';
import D3 from './icons/D3';


import {
    SiAndroidstudio,
    SiC,
    SiExpo,
    SiFirebase, SiFlask,
    SiHeroku,
    SiMaterialUi,
    SiMongodb,
    SiJava,
    SiJavascript,
    SiReact,
    SiReactrouter,
    SiNodeDotJs,
    SiPython,
    SiBootstrap,
    SiCss3,
    SiHtml5,
    SiNpm,
    SiIntellijidea,
    SiVisualstudio,
    SiNextDotJs,
    SiSocketDotIo

} from 'react-icons/si';
import { AiOutlineConsoleSql } from "react-icons/ai";
import { LanguageContext } from '../contexts/LanguageContext';
import IconComponent from './IconComponent';

const Experimental = () => {

    const [goProfile, setGoProfile] = useState(false);
    const [goWork, setGoWork] = useState(false);

    const goToProfile = () => {
        setGoProfile(true);
    }

    const goToWork = () => {
        setGoWork(true);
    }

    return (
        <LanguageContext.Consumer>
            {({ texts }) => {
                return (
                    <div className="container experimental">
                        <div className="col-md-12 first">
                            {texts.hello.greeting}
                        </div>
                        <hr className="hr" />
                        <div className="col-md-12 second py-md-2">
                            {texts.homeText1.text}
                            <br />
                            {texts.homeText2.text}
                            <br /> {texts.homeText3.text}
                        </div>

                        {/*  <div className="col-md-12 buttons-container py-md-2">
                            <div className="btn-group">
                                <Button id="work-button" onClickHere={goToWork}>
                                    {texts.buttonLabelWork.work}
                                </Button>
                                {goWork ? <Redirect to="/work" /> : null}
                                <Button id="pro-button" onClickHere={goToProfile}>
                                    {texts.buttonLabelProfile.profile}
                                </Button>
                                {goProfile ? <Redirect to="/profile" /> : null}
                            </div>

                        </div> */}

                        <hr className="hr" />
                        <div className="col-md-12 py-md-3">


                            <div className="row icons-line">
                                <SiJavascript />
                                <SiReact />
                                <D3 />
                                <SiReactrouter />
                                <SiCss3 />
                                <SiHtml5 />
                                <SiNodeDotJs />
                                <SiNextDotJs />
                                <SiPython />
                                <SiBootstrap />
                                <SiNpm />
                                <SiAndroidstudio />
                                <SiIntellijidea />
                                <SiVisualstudio />
                                <SiC />
                                <SiExpo />
                                <SiFirebase />
                                <SiFlask />
                                <SiHeroku />
                                <SiMaterialUi />
                                <SiMongodb />
                                <SiSocketDotIo />
                                <SiJava />
                                <AiOutlineConsoleSql />
                                <IconComponent />





                            </div>

                        </div>
                    </div>
                )

            }}
        </LanguageContext.Consumer>
    )

}

export default Experimental;