const checkIfBlank = (str) => {
    var cut = '';
    cut = str.trim();
    if (cut.length === 0) {
        return true;
    }
    else {
        return false;
    }
}
//    checkIfBlank('   ');


const isThisAValidEmail = (email) => {
    const regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (email.match(regex)) {
        return true;
    }
    return false;
}


const functions = {
    checkIfBlank: checkIfBlank,
    isThisAValidEmail: isThisAValidEmail,
}

export default functions;