import React from 'react';
import functions from '../functions/MessageSender';
import { LanguageContext } from '../contexts/LanguageContext';
import helpers from '../functions/helpers';
import { CSSTransition } from "react-transition-group";
import { Container, /*Button, */ Alert } from 'react-bootstrap';


class Contactme2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            message: '',
            output: null,
            isPending: false,
            fieldsWereBlank: false,
            messageWasSent: false,
            outputIsVisible: false,
            buttonIsVisible: true,
            emailIsInvalid: false,
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.clearInputs = this.clearInputs.bind(this);
    }

    componentDidMount() {
        // console.log('mounted');
    }
    handleChange(event) {
        this.setState({
            [event.target.name]: event.target.value
        }, () => {
            // console.log("test");
        });
    }

    handleClick(event) {
        event.preventDefault();
        if (helpers.checkIfBlank(this.state.name)
            || helpers.checkIfBlank(this.state.email)
            || helpers.checkIfBlank(this.state.message)) {

            this.setState({
                fieldsWereBlank: true,
                outputIsVisible: true,
                buttonIsVisible: false,
            })
            return;
        }
        else if (!helpers.isThisAValidEmail(this.state.email)) {
            //console.log("Invalid email.");
            this.setState({
                outputIsVisible: true,
                emailIsInvalid: true,
                buttonIsVisible: false,
            })
            return;
        }
        this.postDataIntoAgenda();
        this.clearInputs();
        this.setState({
            sendButtonWasClicked: true,
            outputIsVisible: true,
            buttonIsVisible: false //added - foi isto q corrigiu o prob de o botao ficar ACIMA  da mensagem
        })
    }

    postDataIntoAgenda = () => {
        this.setState({
            isPending: true,
        })
        functions.sendMessage(this.state, this.callback);
    }

    callback = (data, status) => { //talvez retificar o backend pa mandar codigos distintos 
        if (data === 'Message sent!') {
            this.setState({
                requestWasSent: true,
            })
        }
        this.setState({
            messageWasSent: true,
            isPending: false,
        })
    }

    clearInputs = () => {
        this.setState({
            name: '',
            email: '',
            message: '',
        })
    }

    render() {
        return (
            <LanguageContext.Consumer>
                {({ texts }) => {
                    return (
                        <div>
                            <form>
                                <div className="form-group">
                                    <input type="text"
                                        className="form-control form-control-lg"
                                        id="exampleFormControlInput1"
                                        placeholder={texts.formPlaceHolder.placeholderName}
                                        value={this.state.name}
                                        onChange={this.handleChange}
                                        name="name"
                                    />
                                </div>
                                <div className="form-group">
                                    <input type="email" className="form-control form-control-lg"
                                        id="exampleFormControlInput1"
                                        placeholder={texts.formPlaceHolder.placeholderEmail}
                                        value={this.state.email}
                                        onChange={this.handleChange}
                                        name="email"
                                    />
                                </div>
                                <div className="form-group">
                                    <textarea className="form-control form-control-lg"
                                        id="exampleFormControlTextarea1"
                                        rows="5"
                                        placeholder={texts.formPlaceHolder.placeholderMessage}
                                        value={this.state.message}
                                        onChange={this.handleChange}
                                        name="message"></textarea>
                                </div>
                            </form>
                            <div
                                className="outputdiv-container">
                                {this.state.buttonIsVisible &&
                                    (<button type="submit"
                                        className="btn btn-primary btn-lg btn-block "
                                        id="send"
                                        onClick={this.handleClick}>
                                        {texts.sendButton.send}
                                    </button>)}

                                {this.state.outputIsVisible ?
                                    (<CSSTransition
                                        in={this.state.outputIsVisible}
                                        timeout={300}
                                        classNames="alert"
                                        unmountOnExit
                                        onEnter={() => this.setState({ outputIsVisible: false })}
                                        onExited={() => this.setState({ buttonIsVisible: true })}>

                                        <Alert
                                            variant="warning"
                                            dismissible // aciona o x para fechar no canto sup direito
                                            onClose={() => this.setState({
                                                outputIsVisible: false,
                                                buttonIsVisible: true,
                                                name: '',
                                                email: '',
                                                message: '',
                                            })}>
                                            <Alert.Heading>
                                                {texts.alertHeading.alertHeading}
                                            </Alert.Heading>
                                            <p>

                                                {this.state.isPending ?
                                                    texts.formOutput.pending :
                                                    (this.state.messageWasSent ?
                                                        texts.messageSent.messageSent :
                                                        (this.state.fieldsWereBlank ? texts.fillTheFields.fillTheFields :
                                                            this.state.emailIsInvalid ? texts.invalidEmail.invalidEmail :

                                                                null))
                                                }
                                            </p>


                                        </Alert>
                                    </CSSTransition>)
                                    : null
                                }
                            </div>
                        </div>
                    )
                }
                }
            </LanguageContext.Consumer>
        );
    }
}
export default Contactme2;
