import React from 'react';
import Coffee from './icons/Coffee';
import Github from './icons/Github';
import Linkedin from './icons/Linkedin';
import './icons/Icons.css';
import './Navbar.css';
import { LanguageContext } from '../contexts/LanguageContext';

import { AiOutlineLinkedin, AiFillGithub } from "react-icons/ai";
import { Link } from 'react-router-dom';

const Navbar = () => {



    return (
        <LanguageContext.Consumer>
            {({ texts, toggleLanguage }) => {

                return (<div>
                    <nav className="navbar navbar-expand-lg fixed-bottom navbar-dark bg-dark">
                        <a className="navbar-brand" href="#"></a>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon">
                            </span>
                        </button>

                        <div className="collapse navbar-collapse"
                            id="navbarSupportedContent">
                            <ul className="navbar-nav mr-auto">

                                <li className="nav-item active">
                                    <a className="nav-link"
                                        id="default"
                                        href="#">Home
                                <span className="sr-only">(current)</span>
                                    </a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#/profile">{texts.menu.about}</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#/work">{texts.menu.portfolio}</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#/contacts">{texts.menu.contacts}</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#/certifications-and-courses">{texts.menu.certificationsAndCourses}</a>
                                </li>
                                <button className="btn btn-primary shadow-none"
                                    id="toggle-lang-button"
                                    onClick={toggleLanguage}
                                >{texts.menu.language}</button>


                            </ul>

                        </div>
                        <Linkedin />
                        <Github />


                        <Coffee />
                    </nav>
                </div>);
            }}
        </LanguageContext.Consumer>
        )
}

export default Navbar;